@use '../abstracts' as *;

.partners {
	width: 100%;
	padding: 20px 0;
	margin-top: 40px;
	overflow-x: hidden;
	background-color: $color-white;

	.section-heading {
		margin-left: 10vw;
	}

	.logo-wrapper {
		display: flex;
		height: auto;
		width: 1222px;

		img {
			width: 150px;
			margin-left: 100px;
			object-fit: contain;
			animation: slideLeft 10s linear infinite;
		}
	}
}

@keyframes slideLeft {
	0% {
		transform: translateX(0);
	}

	100% {
		transform: translateX(-1222px);
	}
}
